import React, {Component} from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

export default function BasicSelect() {
    const [age, setAge] = React.useState('');
    const [detail, setDetail] = React.useState('');
    const [detailThree, setDetailThree] = React.useState('');
    const [detailFour, setDetailFour] = React.useState('');
  
    const handleChange = (event) => {
        setAge(event.target.value);
    };
    const detailChange = (event) => {
        setDetail(event.target.value);
    };
    const detailThreeChange = (event) => {
        setDetailThree(event.target.value);
    };
    const detailFourChange = (event) => {
        setDetailFour(event.target.value);
    };

    return (
        <Box sx={{ mx: "auto" }}>
            <FormControl fullWidth>
                <InputLabel id="depth-one">1 </InputLabel>
                <Select
                labelId="depth-one"
                id="depth-one-select"
                value={age}
                label="1 "
                onChange={handleChange}
                >
                    <MenuItem value={10}></MenuItem>
                    <MenuItem value={20}></MenuItem>
                    <MenuItem value={30}></MenuItem>
                    <MenuItem value={40}></MenuItem>
                    <MenuItem value={50}></MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-two">2 </InputLabel>
                <Select
                labelId="depth-two"
                id="depth-two-select"
                value={detail}
                label="2 "
                onChange={detailChange}
                >
                    <MenuItem value={10}>1</MenuItem>
                    <MenuItem value={20}>2</MenuItem>
                    <MenuItem value={30}>3</MenuItem>
                    <MenuItem value={40}>4</MenuItem>
                    <MenuItem value={50}>5</MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-three">3 </InputLabel>
                <Select
                labelId="depth-three"
                id="depth-three-select"
                value={detailThree}
                label="3 "
                onChange={detailThreeChange}
                >
                    <MenuItem value={10}>3  1</MenuItem>
                    <MenuItem value={20}>3  2</MenuItem>
                    <MenuItem value={30}>3  3</MenuItem>
                    <MenuItem value={40}>3  4</MenuItem>
                    <MenuItem value={50}>3  5</MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-four">4 </InputLabel>
                <Select
                labelId="depth-four"
                id="depth-four-select"
                value={detailFour}
                label="4 "
                onChange={detailFourChange}
                >
                    <MenuItem value={10}>4  1</MenuItem>
                    <MenuItem value={20}>4  2</MenuItem>
                    <MenuItem value={30}>4  3</MenuItem>
                    <MenuItem value={40}>4  4</MenuItem>
                    <MenuItem value={50}>4  5</MenuItem>
                </Select>
            </FormControl>
        </Box>
    );
}