import React, {Component} from 'react';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import DateRangePicker from '@mui/lab/DateRangePicker';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import Box from '@mui/material/Box';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';

export default function BasicDateRangePicker() {
    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    }));
    
  const [value, setValue] = React.useState([null, null]);

  const [alignment, setAlignment] = React.useState('web');

  const handleChange = (event, newAlignment) => {
    setAlignment(newAlignment);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2}>
            <Grid item xs={6}>
                <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DateRangePicker
                    startText=""
                    endText=""
                    value={value}
                    onChange={(newValue) => {
                    setValue(newValue);
                    }}
                    renderInput={(startProps, endProps) => (
                    <React.Fragment>
                        <TextField {...startProps} />
                        <Box sx={{ mx: 2 }}> - </Box>
                        <TextField {...endProps} />
                    </React.Fragment>
                    )}
                />
                </LocalizationProvider>
            </Grid>
                <Grid item xs={6}>
                    <ToggleButtonGroup 
                    color="primary"
                    value={alignment}
                    exclusive
                    onChange={handleChange}
                    >
                        <ToggleButton value="pick1"></ToggleButton>
                        <ToggleButton value="pick2"></ToggleButton>
                        <ToggleButton value="pick3">15</ToggleButton>
                        <ToggleButton value="pick4"></ToggleButton>
                        <ToggleButton value="pick5"></ToggleButton>
                        <ToggleButton value="pick6"></ToggleButton>
                    </ToggleButtonGroup>
                </Grid>
        </Grid>
    </Box>
  );
}