import React, {Component} from 'react'

const GoodsNotice = (props) => {
    const rows = props.characterData.map((row, index) => {
        return (
            <span key={index}>
                {row.title}
            </span>
        )
    })

    return (
        <p>{rows}</p>
        )
}

class GoodsText extends Component {
    render() {
        const {characterData} = this.props

        return <GoodsNotice characterData={characterData}  />
    }
}

export default GoodsText